import { createRouter, createWebHistory } from "vue-router";
import { routes } from "./routes";

import { supabase } from "@/lib/supabaseClient";

import store from "@/store/index.js";

export const router = createRouter({
  history: createWebHistory(),
  routes: routes,
});

router.beforeEach(async (to) => {
  // Для GitHub Pages
  // if (to.path === "/funerals/") {
  //   return { name: "FuneralsAdd" };
  // }

  // Для Settings
  if (to.name === "FuneralsSettings") {
    return { name: "SettingsTempls" };
  }

  // Для Login
  if (!store.getters.GET_USER) {
    let isAuthenticated = await supabase.auth.getUser();

    store.commit("SET_USER", isAuthenticated.data);

    if (!isAuthenticated.data.user && to.name !== "Login") {
      return { name: "Login" };
    }
  }
});
