import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      sevices: [],
    };
  },
  getters: {
    GET_SERVICES(state) {
      return state.sevices;
    },
  },
  mutations: {
    SET_SERVICES(state, payload) {
      state.sevices = payload;
    },
  },
  actions: {
    async SET_SERVICES(state) {
      await supabase
        .from("SERVICE")
        .select("*")
        .order("id", { ascending: true })
        .then((req) => {
          state.commit("SET_SERVICES", req.data);
        });
    },
    async ADD_SERVICES(state, payload) {
      await supabase.from("SERVICE").insert(payload);
    },
    async EDIT_SERVICES(state, payload) {
      await supabase.from("SERVICE").update(payload[0]).eq("id", payload[1]);
    },
    async DELETE_SERVICES(state, payload) {
      await supabase.from("SERVICE").delete().eq("id", payload);
    },
  },
};
