import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      statistic: [],
    };
  },
  getters: {
    GET_FUNERALS_STATISTIC(state) {
      return state.statistic;
    },
    GET_FUNERALS_STATISTIC_LENGHT(state) {
      return JSON.parse(JSON.stringify(state.statistic)).length;
    },
  },
  mutations: {
    SET_FUNERALS_STATISTIC(state, payload) {
      state.statistic = payload;
    },
  },
  actions: {
    async SET_FUNERALS_STATISTIC(state) {
      await supabase
        .from("FUNERAL")
        .select("*")
        .neq("templ", "false")
        .gte("date", state.getters.GET_DATE_STATISTIC_FILTERS[0])
        .lte("date", state.getters.GET_DATE_STATISTIC_FILTERS[1])
        .order("templ", { ascending: true })
        .then((req) => {
          state.commit("SET_FUNERALS_STATISTIC", req.data);
        });
    },
  },
};
