import { createApp } from "vue";
import App from "./App.vue";
import { router } from "./router/router";

import store from "./store";

import "@/assets/styles.scss";

import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import {
  faCaretLeft,
  faCalendarDays,
  faListUl,
  faChartSimple,
  faPlus,
  faShuffle,
  faPhone,
  faGear,
  faChurch,
  faCross,
  faTruckField,
  faTrash,
  faPencil,
  faRubleSign,
  faCheckCircle,
  faHospital,
  faPersonRays,
  faChartPie,
  faHouse,
  faSquarePollVertical,
  faSquareCheck,
  faChartColumn,
} from "@fortawesome/free-solid-svg-icons";
import { faBitcoin } from "@fortawesome/free-brands-svg-icons";
import { faSquare } from "@fortawesome/free-regular-svg-icons";

library.add(
  faCaretLeft,
  faCalendarDays,
  faListUl,
  faChartSimple,
  faPlus,
  faShuffle,
  faPhone,
  faGear,
  faChurch,
  faCross,
  faTruckField,
  faTrash,
  faPencil,
  faRubleSign,
  faCheckCircle,
  faBitcoin,
  faHospital,
  faPersonRays,
  faChartPie,
  faHouse,
  faSquarePollVertical,
  faSquareCheck,
  faSquare,
  faChartColumn
);

createApp(App)
  .component("font-awesome-icon", FontAwesomeIcon)
  .use(store)
  .use(router)
  .mount("#app");
