import { DaysWeak, MonthsShort, MonthsFull } from "@/assets/daysName";

export default {
  state() {
    return {
      year: undefined,
      month: undefined,
      day: undefined,
    };
  },
  getters: {
    GET_SHORT(state) {
      let dateNow = new Date(state.year, state.month, state.day),
        yearNow = dateNow.getFullYear(),
        monthNow = dateNow.getMonth(),
        dayNow = dateNow.getDate(),
        weekNow = dateNow.getDay();
      return `${DaysWeak[weekNow]} ${dayNow} ${MonthsShort[monthNow]} ${yearNow}`;
    },
    GET_DATE_OBJ(state) {
      return { year: state.year, month: state.month, day: state.day };
    },
    GET_DATE_SUPABASE(state) {
      let dateNow = new Date(state.year, state.month, state.day),
        yearNow = dateNow.getFullYear(),
        monthNow = dateNow.getMonth(),
        dayNow = dateNow.getDate();
      return `${yearNow}-${addZero(monthNow + 1)}-${addZero(dayNow)}`;
    },
    GET_DATE_TODAY_DATE(state) {
      let dateNow = new Date(state.year, state.month, state.day),
        yearNow = dateNow.getFullYear(),
        monthNow = dateNow.getMonth(),
        dayNow = dateNow.getDate(),
        dateNowTime = dateNow.getTime();

      let date = new Date();

      let dateToday = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate()
      ).getTime();

      let dateTomorrow = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate() + 1
      ).getTime();

      let dateAfterTomorrow = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate() + 2
      ).getTime();

      return dateNowTime === dateToday
        ? "Отпевания на СЕГОДНЯ"
        : dateNowTime === dateTomorrow
        ? "Отпевания на ЗАВТРА"
        : dateNowTime === dateAfterTomorrow
        ? "Отпевания на ПОСЛЕЗАВТРА"
        : `Отпевания на: ${dayNow}/${monthNow + 1}/${yearNow}`;
    },
    GET_DATE_TODAY_DATE_ADD(state) {
      let dateNow = new Date(state.year, state.month, state.day),
        yearNow = dateNow.getFullYear(),
        monthNow = dateNow.getMonth(),
        dayNow = dateNow.getDate(),
        dateNowTime = dateNow.getTime();

      let date = new Date();

      let dateToday = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate()
      ).getTime();

      let dateTomorrow = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate() + 1
      ).getTime();

      let dateAfterTomorrow = new Date(
        date.getFullYear(),
        date.getMonth(),
        date.getDate() + 2
      ).getTime();

      return dateNowTime === dateToday
        ? "Добавить на СЕГОДНЯ"
        : dateNowTime === dateTomorrow
        ? "Добавить на ЗАВТРА"
        : dateNowTime === dateAfterTomorrow
        ? "Добавить на ПОСЛЕЗАВТРА"
        : `Добавить на: ${dayNow}/${monthNow + 1}/${yearNow}`;
    },
    GET_DATE_STATISTIC(state) {
      let dateNow = new Date(state.year, state.month, state.day);

      return `Статистика за ${
        MonthsFull[dateNow.getMonth()]
      } ${dateNow.getFullYear()}`;
    },
    GET_DATE_STATISTIC_FILTERS(state) {
      let dateNow = new Date(state.year, state.month, state.day),
        yearNow = dateNow.getFullYear(),
        monthNow = dateNow.getMonth(),
        dateFirst = `${yearNow}-${addZero(monthNow + 1)}-01`,
        dateEnd = `${yearNow}-${addZero(monthNow + 1)}-31`;

      return [dateFirst, dateEnd];
    },
  },
  mutations: {
    SET_TODAY(state) {
      let toDay = new Date();
      state.year = toDay.getFullYear();
      state.month = toDay.getMonth();
      state.day = toDay.getDate();
    },
    UPD_DAY(state, incr) {
      state.day += incr;
    },
    UPD_ALL_DATE(state, payload) {
      state.year = payload.year;
      state.month = payload.month;
      state.day = payload.day;
    },
  },
};

function addZero(num) {
  return num < 10 ? `0${num}` : num;
}
