import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      funeralsPhone: [],
    };
  },
  getters: {
    GET_FUNERALS_PHONE(state) {
      return state.funeralsPhone;
    },
    GET_FUNERALS_PHONE_LENGHT(state) {
      return JSON.parse(JSON.stringify(state.funeralsPhone)).length;
    },
  },
  mutations: {
    SET_FUNERALS_PHONE(state, payload) {
      state.funeralsPhone = payload;
    },
  },
  actions: {
    async SET_FUNERALS_PHONE(state) {
      await supabase
        .from("FUNERAL")
        .select("*")
        .or("phoneLocation.eq.false,phoneTempl.eq.FALSE")
        .order("locationTempl", { ascending: true })
        .order("locationCemetery", { ascending: true })
        .order("locationAddress", { ascending: true })
        .order("time", { ascending: true })
        .then((req) => {
          state.commit("SET_FUNERALS_PHONE", req.data);
        });
    },
    async EDIT_FUNERALS_PHONE(state, payload) {
      await supabase.from("FUNERAL").update(payload[0]).eq("id", payload[1]);
    },
  },
};
