import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      cemeterys: [],
    };
  },
  getters: {
    GET_CEMETERYS(state) {
      return state.cemeterys;
    },
  },
  mutations: {
    SET_CEMETERYS(state, payload) {
      state.cemeterys = payload;
    },
  },
  actions: {
    async SET_CEMETERYS(state) {
      await supabase
        .from("CEMETERY")
        .select("*")
        .order("id", { ascending: true })
        .then((req) => {
          state.commit("SET_CEMETERYS", req.data);
        });
    },
    async ADD_CEMETERYS(state, payload) {
      await supabase.from("CEMETERY").insert(payload);
    },
    async EDIT_CEMETERYS(state, payload) {
      await supabase.from("CEMETERY").update(payload[0]).eq("id", payload[1]);
    },
    async DELETE_CEMETERYS(state, payload) {
      await supabase.from("CEMETERY").delete().eq("id", payload);
    },
  },
};
