import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      funeralsAllocation: [],
    };
  },
  getters: {
    GET_FUNERALS_ALLOCATION(state) {
      return state.funeralsAllocation;
    },
    GET_FUNERALS_ALLOCATION_LENGHT(state) {
      return JSON.parse(JSON.stringify(state.funeralsAllocation)).length;
    },
  },
  mutations: {
    SET_FUNERALS_ALLOCATION(state, payload) {
      state.funeralsAllocation = payload;
    },
  },
  actions: {
    async SET_FUNERALS_ALLOCATION(state) {
      await supabase
        .from("FUNERAL")
        .select("*")
        .eq("templ", "false")
        .order("locationTempl", { ascending: true })
        .order("locationCemetery", { ascending: true })
        .order("locationAddress", { ascending: true })
        .order("time", { ascending: true })
        .then((req) => {
          state.commit("SET_FUNERALS_ALLOCATION", req.data);
        });
    },
    async EDIT_FUNERALS_ALLOCATION(state, payload) {
      await supabase.from("FUNERAL").update(payload[0]).eq("id", payload[1]);
    },
  },
};
