import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      templs: [],
    };
  },
  getters: {
    GET_TEMPLS(state) {
      return state.templs;
    },
  },
  mutations: {
    SET_TEMPLS(state, payload) {
      state.templs = payload;
    },
  },
  actions: {
    async SET_TEMPLS(state) {
      await supabase
        .from("TEMPL")
        .select("*")
        .order("id", { ascending: true })
        .then((req) => {
          state.commit("SET_TEMPLS", req.data);
        });
    },
    async ADD_TEMPLS(_, payload) {
      await supabase.from("TEMPL").insert(payload);
    },
    async EDIT_TEMPLS(_, payload) {
      await supabase.from("TEMPL").update(payload[0]).eq("id", payload[1]);
    },
    async DELETE_TEMPLS(state, payload) {
      await supabase.from("TEMPL").delete().eq("id", payload);
    },
  },
};
