import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      funerals: [],
      funeralsEdit: [],
    };
  },
  getters: {
    GET_FUNERALS(state) {
      return state.funerals;
    },
    GET_EDIT_FUNERALS(state) {
      return state.funeralsEdit;
    },
  },
  mutations: {
    SET_FUNERALS_READ(state, payload) {
      state.funerals = payload;
    },
    GET_EDIT_FUNERALS_READ(state, payload) {
      state.funeralsEdit = payload;
    },
  },
  actions: {
    async SET_FUNERALS_READ(state) {
      await supabase
        .from("FUNERAL")
        .select("*")
        .eq("date", state.getters.GET_DATE_SUPABASE)
        .order("service", { ascending: true })
        .order("time", { ascending: true })
        .then((req) => {
          state.commit("SET_FUNERALS_READ", req.data);
        });
    },
    async ADD_FUNERALS_READ(_, payload) {
      await supabase.from("FUNERAL").insert(payload);
    },
    async GET_EDIT_FUNERALS_READ(state, payload) {
      await supabase
        .from("FUNERAL")
        .select("*")
        .eq("id", payload)
        .then((req) => {
          state.commit("GET_EDIT_FUNERALS_READ", req.data[0]);
        });
    },
    async EDIT_FUNERALS_READ(_, payload) {
      await supabase.from("FUNERAL").update(payload[0]).eq("id", payload[1]);
    },
    async DELETE_FUNERALS(_, payload) {
      await supabase.from("FUNERAL").delete().eq("id", payload);
    },
  },
};
