import { supabase } from "@/lib/supabaseClient";

export default {
  state() {
    return {
      user: undefined,
    };
  },
  getters: {
    GET_USER(state) {
      return state.user;
    },
  },
  mutations: {
    SET_USER(state, payload) {
      state.user = payload;
    },
  },
  actions: {
    async SET_USER(state) {
      let userInfo = await supabase.auth.getUser();
      state.commit("SET_USER", userInfo.data);
    },
  },
};
